<template lang="pug">
    div.content-help-detail
        iframe(frameborder=0 :width="w_w" :height="w_h" :src="`../../wap/index.php?ctl=settings&act=faq_show&id=${id}`")
</template>
<script>
export default {
    name: 'settingHelpDetail',
    data() {
        return {
            id: '',
            w_w: '',
            w_h: ''
        }
    },
    created() {
        this.id = this.$route.query.id;
    },
    mounted() {
        this.w_w = $(window).width();
        this.w_h = $(window).height();
        document.title = this.$route.query.title;
    }
}
</script>


